html, body {
    height: 100%;
    width: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
    height: 100%;
}

h1, h2, h3, h4, h5, h6 {
    text-align: center;
}

.App {
    height: 100%;
}

.Entry, .WorkoutA, .WorkoutB {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    align-content: space-around;
    height: 100%;

    header {
        flex-grow: 1;
        flex-basis: 20%;
    }

    .BigFlippingButton {
        text-align: center;
        flex-grow: 1;
        flex-basis: 40%;
        border: 1px solid black;
        font-size: 50vw;
        text-decoration: none;
        margin: 10px;
        border-radius: 10px;
    }
}

    .JustAFlippingButton {
        text-align: center;
        border: 1px solid black;
        font-size: 30px;
        line-height: 50px;
        text-decoration: none;
        margin: 10px;
        display: block;
        width: calc(100% - 20px);
        border-radius: 10px;
        height: 50px;
    }

.Workout {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
    align-content: space-around;
    height: 100%;

    .Content {
        flex-grow: 1;
        flex-basis: 80%;

        height: 100%;
        width: calc(100% - 20px);
        max-width: calc(100% - 20px);

        margin: 10px;

        .Timer {
            text-align: center;
            height: 10%;
        }

        .Title {
            text-align: center;
            height: 20%
        }

        .Image {
            height: 70%;
            width: 100%;
            max-width: 100%;

            img {
                width: 100%;
                max-width: 100%;

                height: 100%;
                max-height: 100%;
            }
        }
    }

    .BigFlippingButton {
        @extend .JustAFlippingButton;
    }
}
